@import url(https://fonts.googleapis.com/css?family=Sawarabi+Mincho&display=swap&subset=japanese,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Abril+Fatface&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Serif+JP:200,400&display=swap&subset=japanese);
*,::before,::after{background-repeat:no-repeat;box-sizing:border-box}body{margin:0;font-family:'Sawarabi Mincho', sans-serif;letter-spacing:0.5em;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;font-size:14px;line-height:1.3;min-width:300px;background:#fcfaf2}button,input,select,textarea{font-family:inherit;font-size:100%;border-style:none}:focus{outline:none}.center{text-align:center}a{color:#e83015}h1{font-size:3rem;font-weight:400}h2{font-size:2rem;font-weight:400}.text--small{font-size:0.9em;letter-spacing:0.15em}.text--green{color:#838a2d}.app-main{padding:0 20px;width:100%}.grid-container{width:100%;max-width:1200px;margin:20px auto}.paper{height:100% !important}.grid-content{width:100%}.grid-content--title{font-size:1.2em}.grid-content--image{width:100%;height:0;padding-top:60%;overflow:hidden;position:relative}.grid-content--image img{position:absolute;width:100%;height:100%;top:0;left:0;object-fit:cover}.grid-content--detail{padding:0px 5px}.grid-content--address{font-size:0.9em;letter-spacing:0.2em}.MuiPaper-rounded{overflow:hidden;border-radius:0 !important;border:2px #000 solid;background:#fff;padding:8px}

